body {
  margin: 0;
  padding: 0;
  /* Чтобы скрыть контент во время загрузки */ }
  body .preloader-active {
    overflow: hidden; }

.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #fff;
  /* Цвет фона */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 58px 0;
  background: #1a1a1a;
  z-index: 1000;
  animation: fadeOut 1s ease-in-out 1s forwards; }

.spinner {
  animation: spin 2s linear infinite; }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes fadeOut {
  0% {
    opacity: 1; }
  /* Начальная непрозрачность */
  100% {
    opacity: 0; }
  /* Конечная непрозрачность (исчезновение) */ }
